<template>
    <div>
        <input v-model="loginInfo.usrname" placeholder="请输入用户名"/>
        <input v-model="loginInfo.password" placeholder="请输入密码" type="password"/>
        <button v-on:click="login">登录</button>
        <pre>{{content}}</pre>
    </div>
</template>

<script>
export default {
    name: 'PageLogin',
    data() {
        return {
            loginInfo :{
                usrname: '',
                password: ''
            },
            content:'默认内容'
        }
    },
    methods: {
        login: function(){
            this.$axios
                .post('/login', {
                    username: this.loginInfo.usrname,
                    password: this.loginInfo.password
                })
                .then(successResponse =>{
                    this.responseResult = JSON.stringify(successResponse.data)
                    if(successResponse.data.code === 200) {
                        this.$router.replace({path:'/'})
                    }
                })
                .catch(failResponse => {})
                
        }
    },
    mounted() {
        this.axios.post("http://api.komavideo.com/news/list")
            .then(body =>{
                this.content = body.data;
            })
    }
}
</script>


